import React, { ReactElement, ReactNode, useState } from "react";
import { useSelector } from "react-redux";
import authHeader from "../../Authentication/authenticationHeader";
import { Config } from "../../App/globalVariables";
import { RootState } from "../../Reducer/interfaces";
import { AuthState } from "../../Reducer/interfaces/auth/authInterfaces";
import { userAuthActions } from "../../Reducer/services/auth/authenticationServices";
interface IProps {
    setLoginVisibility: (value: boolean) => void;
}

const RocketGame = ({ setLoginVisibility }: IProps): ReactElement => {
    const auth: AuthState = useSelector((state: RootState) => state.auth);
    const [error, setError] = useState("");
    return (
        <div className="container game-container">
            <h1>Rocket Game</h1>
            <img
                src={
                    require("../../Images/RocketGame/MainScreen.png") as string
                }
                alt="Main Screen"
            />
            <p>{`This game is all about flying a rocket to its destination. You do not have to be an astronaut in this case as you can fly the rocket using the buttons "a", "d" and "space". Explore all the 40 unique levels and try to maneuver around the obstacles. These levels together take 90 minutes to complete. The faster you try to go the longer it seems to take to complete all the levels. `}</p>
            <img
                src={
                    require("../../Images/RocketGame/LevelSelector.png") as string
                }
                alt="Level Selector"
            />
            <p>{`The rocket has to avoid getting hit by obstacles, dodge flying plant-bullets, fly in narrow spaces and even steer clear of invisible walls. This might seem easy and straightforward, but it requires a skilled pilot in order to complete all levels. Some levels even require some thinking along with controlling the rocket. The levels will ramp up in difficulty where some levels require fast flying in order to get to the next level.`}</p>
            <img
                src={
                    require("../../Images/RocketGame/DifferentThemes.png") as string
                }
                alt="Different themes of the game"
            />
            <ul>
                <li>40 Levels</li>
                <li>4 Themes with 10 levels each</li>
                <li>5 Mechanics that will make the levels more interesting</li>
                <li>A rocket that does not like to be touched</li>
            </ul>
            <img
                src={
                    require("../../Images/RocketGame/FlowerFire.png") as string
                }
                alt="Shooting flower mechanic"
            />
            {error ? (
                <div className="glass-box declined contact-form-submission-text">
                    {error}
                </div>
            ) : null}
            {getButton()}
        </div>
    );

    function getButton(): ReactNode {
        if (auth.loggedIn) {
            return (
                <button
                    onClick={() => downloadGame()}
                    className="game-download-button rgb-button login-button transparent-button white-text"
                >
                    <span>Download Game</span>
                </button>
            );
        } else {
            return (
                <button
                    className="general-button button-blocked"
                    onClick={() => {
                        setLoginVisibility(true);
                    }}
                >
                    {"Download Game"}
                </button>
            );
        }
    }

    function downloadGame() {
        const header = {
            ...{ "Content-Type": "application/json" },
            ...authHeader(),
        };

        const requestOptions: RequestInit = {
            method: "GET",
            headers: header,
        };

        void fetch(`${Config.apiUrl}/download/rocketGame`, requestOptions).then(
            (response) => {
                void response.blob().then((blob) => {
                    if (!response.ok) {
                        if (response.status === 401) {
                            // auto logout if 401 response returned from api
                            void userAuthActions.logout();
                            location.reload();
                        } else if (response.status === 408) {
                            void userAuthActions.generateAccessToken();
                        } else if (response.status === 429) {
                            setError(
                                "You have sent too many requests, please wait!"
                            );
                        }
                    } else {
                        const url = window.URL.createObjectURL(blob);
                        const a = document.createElement("a");
                        a.href = url;
                        a.download = "RocketGameInstaller.exe";
                        a.click();
                    }
                });
            }
        );
    }
};

export default RocketGame;
